.header_7_pagePart{font-size:1rem;display:flex;flex-direction:row;justify-content:space-between;align-items:center;position:fixed;z-index:100;gap:20px;top:0;left:50%;transform:translateX(-50%);width:90%;padding:10px;margin-top:10px;border:1px solid var(--first-border-color, gray);border-radius:8px;background-color:var(--second-background-color, black)}.header_7_pagePart .logo{height:30px}.header_7_pagePart .actions{display:flex;flex-direction:row;gap:10px}.header_7_pagePart .link{display:flex;flex-direction:row;align-items:center;font-size:1em;gap:8px;background:none;padding:8px 16px;border:1px solid var(--first-border-color, gray);border-radius:99999px;color:rgb(var(--first-text-color, 255, 255, 255))}.header_7_pagePart .link .icon{width:1em;aspect-ratio:1/1}.header_7_pagePart .link_signUp{background-color:var(--primary-color, teal);color:rgb(var(--first-text-color, 255, 255, 255))}